export const mustFilled = (value) => (value) ? null : '     '
export const phoneValidate = (value) => {
    if (!value) return '     '
    else if (!/(^(09|9)\d{9}$)/.test(value))
        return ' '
    else return null
}

export const showPrice = (price) =>  price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");

